$cell-size: 100px;
$gap-size: 1px;
$width: 7 * $cell-size + 10px;
$height: 5 * $cell-size + 10px;
$cell-color: #eee;
$cell-hover: pink;
$month-lenght--28: 28;
$month-lenght--29: 29;
$month-lenght--30: 30;
$month-lenght--31: 31;
$days:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;

